@use 'partials' as *;
*, *:before, *:after{
    box-sizing: border-box;
}
html{
    font-size: 10px;
}
body,h1, h2, h3, h4, h5,h6, p{
    margin: 0;
}
body{
    display: grid;
    place-items: center;
    min-height: 100vh;
    font-size: 1.5rem;
    background: $main-bg;
    color: $main-head;
}

.container{
  display: flex;
  flex-flow: row wrap;
  width: 67%;


   .left{
       display: flex;
       flex-flow: column;
       align-items: center;
       justify-content: space-around;
       width: 50%;
        background: $card-bg;
        padding: 2rem 0;
        border-radius: 1.6rem 0 0 1.6rem;
        order: 1;
     .top{
       
         width: 70%;
         
        h1{
            font-family: $font-inter;
            font-weight: 700;
            margin-bottom: 2rem;
            
            .insights{
            color: $accent;
        }
        }

        p{
            font-family: $font-inter;
            line-height: 1.5;
            padding-right: 1rem;
            color: $para;
            font-weight: 400;
        }
   
      }

      .bottom{
          display: flex;
          flex-flow: row wrap;  
          justify-content: space-between;
          align-items: center;
          width: 70%;
          padding-right: 4rem;
     
        //   background: orange;

        .stat{
            h3{
               font-weight: 700;
               color: $main-head;
               font-family: $font-inter;

            }
            p{
                font-weight: 400;
                color: $stat-head;
                font-family: $font-inter;
                text-transform: uppercase;
                font-size: 1.2rem;
                letter-spacing: .15rem;
            }
        }

      }
    }
   .right{
     width: 50%;
     background: $accent;
     border-radius: 0rem 1.6rem 1.6rem 0rem;
     order: 2;
     
     .image-desktop{
        display: flex;
        align-items: stretch;
        border-radius: 0rem 1.6rem 1.6rem 0rem;
        // background: ;

        img{
            align-self: center;
            mix-blend-mode: multiply;
            opacity: .8;
            border-radius: 0rem 1.6rem 1.6rem 0;
           width: 100%;
            height: 100%;

        }
     }

     .image-mobile{
         display: none;
     }
   }

}

@media screen and(min-width: 1120px) and (max-width: 1315px) {
    .container{
        width: 80%;
    }
}

@media screen and(min-width: 1005px) and (max-width: 1120px) {
    .container{
        width: 90%;
    }
}


@media screen and (max-width: 1005px) {
    .container{
        flex-direction: column;
        align-items: center;
        width: 90%;
        margin: 4rem 0;
        

        .left{
            width: 70%;
            height: 50%;
            order: 2;
            border-radius: 0 0 1.6rem 1.6rem;
            margin-top: -4px;

            .top{
                width: 90%;
                text-align: center;

                p{
                    margin-bottom: 4rem;
                }
            }

            .bottom{
             
                width: 90%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

             .stat{
                 align-items: center;
                 text-align: center;
                 margin-bottom: 4rem;
                 margin-left: 3rem;
            
             }
            }
        }

        .right{
            display: flex;
            flex-flow: column;
            justify-content: center;
            align-items: center;
            width: 70%;
            border-radius: 1.6rem 1.6rem 0 0;
            order: 1;

        
 
             .image-mobile{
                 display: block;

                 img{
                 
                    align-self: center;
                    mix-blend-mode: multiply;
                    opacity: .8;
                    border-radius: 1.6rem 1.6rem 0 0;
                    max-width: 100%;
                    min-height: 100%;
                 }
             }

            .image-desktop{
                display: none;
            }
        }
    }
}